import React from 'react'
import styles from './MagicBox.module.css'
import classNames from 'classnames'
import Pulse from '../Pulse'

class MagicBox extends React.Component {
  constructor(props) {
    super(props)

    const { backgroundColor, highlightText } = this._generateRandomColor()

    this.state = {
      style: {
        backgroundColor,
      },
      highlightText,
      pulses: {},
      counter: 0,
    }
  }

  render() {
    const { style, textStyle, highlightText, pulses } = this.state

    const className = classNames({
      [styles.Container]: true,
      [styles.HighlightText]: highlightText,
    })

    return (
      <div
        className={className}
        ref={this._setContainerRef}
        style={style}
        onClick={this._onClick}
        onMouseMove={this._onMouseMove}
        onMouseLeave={this._onMouseLeave}
      >
        <span className={styles.Text} style={textStyle}>
          Click Me!
        </span>
        {Object.keys(pulses).map(key => {
          const { x, y } = pulses[key]

          return <Pulse key={key} x={x} y={y} />
        })}
      </div>
    )
  }

  componentWillUnmount() {
    const { pulses } = this.state

    Object.keys(pulses).forEach(key => {
      clearTimeout(pulses[key].disconnect)
    })
  }

  _container = null

  _setContainerRef = element => {
    this._container = element
  }

  _onClick = event => {
    const offset = this._getOffset(event)
    const { pulses, counter } = this.state

    this._setRandomColor()
    this._createPulse({
      ...offset,
      pulses,
      counter,
    })
  }

  _onMouseMove = event => {
    if (!this._sizes) {
      return null
    }

    const { x, y } = this._getOffset(event)
    const { width, height } = this._sizes
    const halfWidth = width / 2
    const halfHeight = height / 2
    const deltaX = x > halfWidth ? x - halfWidth : -(halfWidth - x)
    const deltaY = y > halfHeight ? y - halfHeight : -(halfHeight - y)
    const tensionX = deltaX / halfWidth
    const tensionY = deltaY / halfHeight
    const maxDeviationX = 120
    const maxDeviationY = 60

    this.setState({
      textStyle: {
        left: maxDeviationX * tensionX,
        top: maxDeviationY * tensionY,
      },
    })
  }

  _onMouseLeave = event => {
    const { x, y } = this._getOffset(event)
    const { width, height } = this._container.getBoundingClientRect()

    if (x < 0 || x > width || y < 0 || y > height) {
      this.setState({
        textStyle: {
          left: 0,
          top: 0,
        },
      })
    }
  }

  _createPulse({ x, y, pulses, counter }) {
    const increment = counter + 1

    pulses[increment] = {
      disconnect: setTimeout(() => {
        delete pulses[increment]

        this.setState({
          pulses,
        })
      }, 5000),
      x,
      y,
    }

    this.setState({
      counter: increment,
      pulses,
    })
  }

  _setRandomColor() {
    const { backgroundColor, highlightText } = this._generateRandomColor()

    this.setState({
      style: {
        backgroundColor,
      },
      highlightText,
    })
  }

  _generateRandomColor() {
    const { r, g, b, a } = this._getRandomRGBA()

    const opacity = Math.sqrt(a)
    const darkest = [this._pow(r), this._pow(g), this._pow(b)].reduce(
      (pv, cv) => pv + cv,
      0
    )

    return {
      backgroundColor: `rgba(${r},${g},${b},${opacity})`,
      highlightText: darkest > 123456 && opacity > 0.78,
    }
  }

  _getRandomRGBA() {
    return {
      r: this._getRandomInt(0, 256),
      g: this._getRandomInt(0, 256),
      b: this._getRandomInt(0, 256),
      a: this._getRandomArbitrary(0, 1),
    }
  }

  _getRandomArbitrary(min, max) {
    return Math.random() * (max - min) + min
  }

  _getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min
  }

  _pow(v) {
    return Math.pow(256 - v, 2)
  }

  _getOffset({ pageX, pageY, target }) {
    return {
      x: pageX - target.offsetLeft,
      y: pageY - target.offsetTop,
    }
  }

  get _sizes() {
    if (!this._container) {
      return null
    }

    const { width, height } = this._container.getBoundingClientRect()

    return {
      width,
      height,
    }
  }
}

export default MagicBox



